.file-list {
  overflow-y: auto;
  max-height: 90vh;
}

.notification {
  position: absolute;
  z-index: 2000;
  bottom: 20px;
  right: 20px;
  background-color: #95AFCD;
  padding: 10px;  
}

.notification-header {
  font-size: 1.25em;  
  font-weight: 500;
}

.success {
  background-color: lightgreen;
}

.failure {
  background-color: lightcoral;
}