.data-header {
  font-weight: 400;
  font-size: 20px;
  color: #95afcd;
}

.data-large {
  color: red;
  font-weight: bold;
  font-size: 24px;
}

.container-md {
  background-color: black;
}
.graphics-center {
  height: 80vh;
}

.file-item {
  padding: 10px;
  font-weight: 300;
  border: 3px solid #95afcd;
  border-bottom: none;
  cursor: pointer;
  color: white;
  &:hover {
    background-color: #95afcd;
  }
  &:nth-last-child(1) {
    border-bottom: 3px solid #95afcd;
  }
}

.graphics-display {
  outline: none;
  z-index: 200px;
}

.overlay {
  position: absolute;
  pointer-events: none;
  z-index: 200;
  right: 0;
  top: 0;
  left: 0;
  bottom: 0;
}

.loader-container {
    pointer-events: none;
    position: absolute;
    z-index: 1000;
    margin-left: auto;
    margin-right: auto;
    left: 0;
    right: 0;
    top: 30%;
}

.tick {
  width: 3.2rem;
  &:first-child {
    width: auto;
    transform: translate(-50%, 0%);
  }

  &:last-child {
    width: auto;
    transform: translate(50%, 0%);
  }
}

.tick-line {
  min-height: 2vh;
  background-color: #95afcd;
  width: 2px;
}

$slider-radius: 25px;
$slider-handle-radius: 20px;

.slider-container {
  margin-top: auto;
}

div .slider {
  pointer-events: all;

  .noUi-target {
    height: 7vh;
    border-radius: $slider-radius;
    background-color: #4d4d4d;
    border-color: #4d4d4d;
    box-shadow: none;
  }
  .noUi-handle {
    height: calc(7vh + 10px);
    background: transparent;
    border: 7px solid #95afcd;
    border-radius: $slider-handle-radius;
    box-shadow: none;
    cursor: pointer;
  }
  .noUi-handle::before {
    left: 0px;
    background: transparent;
  }
  .noUi-handle::after {
    bottom: 0px;
    top: unset;
    left: 50%;
    height: 50%;
    width: 4px;
    background-color: #95afcd;
    transform: translate(-100%);
  }
  .noUi-connect {
    background: #95afcd;
    border-top-left-radius: $slider-handle-radius;
    border-bottom-left-radius: $slider-handle-radius;
  }
  .noUi-tooltip {
    background: transparent;
    color: #ffffff;
    left: 300%;
    width: 200px;
    bottom: 20%;
    border: none;
    font-weight: bold;
    font-size: 22px;
  }
}

.dropdown {
    pointer-events: all;
    margin: 5px;
}

div .small-slider {
  pointer-events: all;
  height: 25vh;
  justify-content: flex-end;
  margin: 20px;

  .noUi-target {
    background-color: #4d4d4d;
    border-color: #4d4d4d;
    box-shadow: none;
    flex: 1 1;
  }
  .noUi-handle::before {
    left: 0px;
    background: transparent;
  }
  .noUi-handle::after {
    background: transparent;
  }
  .noUi-handle {
    height: calc(30px);
    background: #95afcd;
    border: 7px solid #95afcd;
    border-radius: $slider-handle-radius;
    box-shadow: none;
    cursor: pointer;
  }
  .noUi-connect {
    background: #95afcd;
    border-bottom-right-radius: $slider-handle-radius;
    border-bottom-left-radius: $slider-handle-radius;
  }
}

div.horizontal.slider {
  margin-top: 10px;
  pointer-events: all;
  .noUi-base {
    width: 150px;
    cursor: pointer;
  }

  .noUi-target {
    height: 2vh;
    border-radius: $slider-radius;
  }
  .noUi-handle::before {
    left: 0px;
    background: transparent;
  }
  .noUi-handle::after {
    background: transparent;
  }

  .noUi-connect {
    background: #95afcd;
    border-radius: $slider-handle-radius;
  }

  .noUi-handle {
    height: calc(1vh + 10px);
    background: transparent;
    top: -2px;
    border: 4px solid #95afcd;
    cursor: grab;
  }
}
