.App {
  text-align: center;
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: black;
  transition: transform 200ms ease 0s;
  user-select: none; 
  overflow-y: auto;
  z-index: 100;
}

.navbar a, .navbar div, .navbar i {
  color: #95AFCD;
  font-weight: 500;
}

.pointer {
  cursor: pointer;
}

.space-between {
  justify-content: space-between;
}

.navbar {
  border-bottom: 2px solid #95AFCD
}

.flex {
  display: flex;
  flex-wrap: wrap;
  max-height: 100%;
  word-break: break-word;
}

.column {
  flex-direction: column;
}


.fill {
  flex: 1 1
}

.fit {
  flex: 0 1
}
a {
  color: white;
}

a:hover {
  color: #95AFCD;
  text-decoration: none;
}


.button-main {
  color: white;
  background-color: rgb(149, 175, 205, 0);
  border: 4px solid rgb(149, 175, 205);
  pointer-events: all;
  cursor: pointer;
  padding: 0 10px;
  border-radius: 5px;
  margin-top: 5px;
  margin-bottom: 5px;
}

.button-large {
    font-size: 25px;
}

.button-main:hover{
  color: white;
  background-color: rgb(149, 175, 205, 0);
}
.center {
  justify-content: center;
  align-items: center;
  align-content: center;
  text-align: center;
  vertical-align: center;
}

.left {
  justify-content: flex-start;
  align-items: center;
  align-content: flex-start;
  text-align: center;
  vertical-align: center;
}

.right {
  justify-content: flex-end;
  align-items: center;
  text-align: end;
  vertical-align: center;
}

div .top {
  align-items: flex-start;  
  vertical-align: flex-start;
  align-content: flex-start;
}

.header, .text {
  color: white;
}